.hero-title {
  color: #2f5233;
  font-size: 4rem;
  font-weight: 500;
  line-height: 4.5rem;
  margin-bottom: 3rem
}
.hero-subtitle {
  color: #2f5233;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 2.5rem;
  margin: 3rem auto;
  max-width: 50rem;
}
.hero-image {
  background-image: url();
  border-radius: 2rem;
  margin: 0 auto;
  width: 100%;

  max-width: 600px;
}

@media (min-width: 600px) {
  .hero-image {
    padding-bottom: 0;
    height: 270px;
  }
}